import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import priv from './modules/priv'
import building from './modules/building'

export default new Vuex.Store({
  state: {
    token:'',
    username:'',
  },
  getters: {
  },
  mutations: {
    // 登录后进行储存
    gettoken(state,num){
      state.token = num
    },
    getusername(state,username){
      state.username = username
    },
    // 退出后进行清空
    totoken(state,num){
      state.token = num
    },
    tousername(state,username){
      state.username = username
    },
  },
  actions: {
  },
  modules: {
    priv,
    building
  },
})
