import axios from 'axios'
import { Message } from 'element-ui';

let baseURL = 'http://estate.eshareedu.cn/estate/api/'

//封装
const resp = (url,data={},params={},method='get') =>{
    return new Promise((resilve,reject)=>{
        axios({
            method: method,
            url: url,
            data: data,
            baseURL: baseURL,
            params: params,
            // headers: {
            //     Authorization:token
            // },
            timeout: 10000,
          }).then((res)=>{
            resilve(res)
          }).catch((rerrn)=>{
            reject(rerrn)
          })
    })
}

const get = (url,params) =>{
    return resp(url,{},params,'get')
}
const post = (url,data,params) =>{
    return resp(url,data,params,'post')
}
export default{
    get,
    post
}

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  config.headers.Authorization = sessionStorage.getItem('token')

  return config;
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error);
});

  // 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么
  return response.data;
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  if(error.code == 'ERR_NETWORK' || error.code == 'ECONNABORTED'){
    Message.error('网络超时')
  }
  return Promise.reject(error);
});