import Vue from 'vue'
import VueRouter from 'vue-router'
import Longin from '../views/Longin.vue'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/longin'
  },
  {
    path: '/longin',
    name: 'longin',
    component: Longin
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    children:[
      // 默认栏
      {
        path: '/',
        redirect:'/com'
      },
      {
        path: '/com',
        name: 'com',
        component: () => import('../components/com.vue')
      },
      // 缴费
      {
        path: '/fee',
        name: 'fee',
        component: () => import('../components/fee/fee.vue')
      },
      // 管理员
      {
        path: '/administrator',
        name: 'administrator',
        component: () => import('../components/Administrator.vue')
      },
      //角色管理
      {
        path: '/administratorrole',
        name: 'Role',
        component: () => import('../components/Role.vue')
      },
      //公告
      {
        path: '/news',
        name: 'news',
        component: () => import('../components/News.vue')
      },
      //楼栋管理   
      {
        path: '/building',
        name: 'building',
        component: () => import('../components/Building.vue')
      },
      //房间管理   
      {
        path: '/house',
        name: 'house',
        component: () => import('../components/House/House.vue')
      },
      //居民管理   
      {
        path: '/Userinfo',
        name: 'Userinfo',
        component: () => import('../components/userinfo/Userinfo.vue')
      },
      //投诉管理   
      {
        path: '/Questions',
        name: 'Questions',
        component: () => import('../components/questions/Questions.vue')
      },
      //报修管理   
      {
        path: '/Repairs',
        name: 'Repairs',
        component: () => import('../components/repairs/Repairs.vue')
      },
      //职员管理   
      {
        path: '/staffs',
        name: 'staffs',
        component: () => import('../components/staffs/staffs.vue')
      },
      // 门店管理
      {
        path: '/stores',
        name: 'stores',
        component: () => import('../components/stores/StoresView.vue')
      },
      // 图片管理
      {
        path: '/advertisement',
        name: 'advertisement',
        component: () => import('../components/advertisement/AdvertiseView.vue')
      },
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = new VueRouter({
  routes
})
// 全局路由守卫
router.beforeEach((to,from,next)=>{
  if(to.path == '/longin'){
  next();
  }else{
    let token = sessionStorage.getItem('token')
    if(!token){
      alert('您还没有登录，请先登录');
      next('/longin');
    }
    next()

  }
  })
  

export default router
