//公共函数的方法
// 防抖

function debounce(fn,delay = 500){
    //定义一个定时器
    let timer = null

    //返回一个回调
    return function(){
        if(timer){
            clearTimeout(timer)
        }
        timer = setTimeout(()=>{
            fn.apply(this,arguments)
            timer = null
        },delay)
    }
}

export {debounce}