const state = {
    menus:'',
}
const mutations = {
    SETMENU(state,menus){
        state.menus = menus?JSON.stringify(menus):[]
    },
    GETMENU(){
       return state.menus.length>0?JSON.parse(state.menus):[]
    }
}
const getters = {
    getleftpriv(state){
        let menus = state.menus.length>0?JSON.parse(state.menus):[]
        return menus.filter(imet=>imet.pid==0 && imet.postion == 'left')
    },   
    gettoppriv(state){
        let menus = state.menus.length>0?JSON.parse(state.menus):[]
        return menus.filter(imet=>imet.pid==0 && imet.postion == 'top')
    }   
}

export default {
    namepaced:true,
    state,
    mutations,
    getters
}