import store from '../store'

const getownpriv = (moudulename) =>{
    // console.log(store.state.priv)
    let menus = store.state.priv.menus?JSON.parse(store.state.priv.menus):[]
    let module = menus.filter(item=>item.name==moudulename)
    // return console.log(menus)
    return menus.filter(item=>item.pid==module[0].id)
}

const haveown = (power,name)=>{

    return power.filter(item=>item.name==name).length?true:false
    
}

export default{
    getownpriv,
    haveown
}