<template>
    <el-header>
        <div class="left">
            <img src="@/assets/logo2.png" alt="">
            <div class="left-right"> 
                <span @click="goindex" class="sho">
                    <i :class="['iconfont', 'icon-home2',{homechange:this.$route.path == '/com'}]"></i>
                    <span :class="{homechange:this.$route.path == '/com'}">首页</span>
                </span>
                <span style="font-size:13px;margin-left: -30px;"><span style="font-weight: bold;margin-right: 4px;">{{this.$store.state.username}}</span>,{{this.nam}}</span>
            </div>
        </div>
        <div class="right">
            <div class="right-left">
                    <el-dropdown trigger="click">
                    <span class="el-dropdown-link">
                        <i class="iconfont icon-shezhixitongshezhigongnengshezhishuxing" ></i>
                        <span>设置</span>
                    </span>
                    <el-dropdown-menu slot="dropdown" >
                        <el-dropdown-item v-for="(imet,index) in this.$store.getters['gettoppriv']" 
                        :key="index" 
                        :class="['iconfont',imet.ico]" 
                        @click.native="$router.push(imet.url,()=>{})"> {{imet.name}} </el-dropdown-item>
                    </el-dropdown-menu>
                    </el-dropdown>
                
            </div>
            <div @click="long">
                <i class="iconfont icon-tuichu"></i>
                <span>退出登录</span>
            </div>
        </div>
  </el-header>
</template>

<script>
export default {
    name:'LeftAside',
    data(){
        return{
            data:new Date(),
            nam:''
        }
    },
    created(){
        this.getst()
    },
    methods:{
        getst(){
            if(this.data.getDay() == 1){
                this.nam = '周一早上好，送你清爽的问候，温馨的祝愿，早晨，美妙的开端，祝你今日精力棒棒，生机多多，心境好好，一切都好！'
            }else if(this.data.getDay() == 2){
                this.nam = '清晨到，对着镜子照一照，笑一笑，所有烦恼都跑掉，抑郁忧愁全都消，快乐自然不会少。问候跟着来报到，给你道声早，祝你一天幸福乐逍遥。'
            }else if(this.data.getDay() == 3){
                this.nam = '以美好的心，欣赏周遭的事物；以真诚的心，对待每一个人；以负责的心，做好份内的事；以谦虚的心，检讨自己的错误；以不变的心，坚持正确的理念。'
            }else if(this.data.getDay() == 4){
                this.nam = '一个人的宽容，来自一颗善待他人的心；一个人的涵养，来自一颗尊重他人的心，才能得到别人的尊重。愿你开心快乐每一天!'
            }else if(this.data.getDay() == 5){
                this.nam = '好心情才会有好风景，好眼光才会有好发现，好思考才会有好主意。人可以不美丽，但要健康；人可以不伟大，但要快乐；人可以不完美，但要追求。'
            }else if(this.data.getDay() == 6){
                this.nam = '.愁要抛，烦要躲，压力别再心里搁；泡泡澡，逗逗鸟，既要舒服又要乐；品佳肴，小酒喝，生活美美笑容多；祝福语，手中握，祝你周六好洒脱'
            }else{
                this.nam = '.美丽的风景，美丽的故事，美好的日子，美好的牵挂；牵挂着你是否一切美好、开心依旧。周末到，送上问候，希望你能更快乐！'
            }
        },
        // 退出登录
        long(){
            this.$confirm('此操作将退出登录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.$router.push('longin')
                // 退出后
                this.$store.commit('totoken','')
                this.$store.commit('tousername','')
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            // console.log(this.data.getDay())
            });
        },
        //页面切换
        goindex(){
            if(this.$route.path != '/com'){
                this.$router.push('com')
            }
        }
    }
}
</script>
<style lang="less" scoped>

.sho{
    cursor: pointer;
    .homechange{
    color:blue; 
    font-weight: bold;
    }
}

</style>