<template>
  <div class="all">
      <div class="bom">
          <div class="box">
          <div class="img">
            <img src="../assets/toxiang.jpg" alt="">
          </div>
          <div class="long">
            <el-form :rules="rules" ref="ruleForm" :model="form" label-width="80px">
              <el-form-item label="账号：" style="width:90%;" prop="username">
                <el-input v-model="form.username" placeholder="请输入账号"></el-input>
              </el-form-item>
              <el-form-item label="密码：" style="width:90%;" prop="pass">
                <el-input v-model="form.pass" type="password" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item style="margin-left: 80px;">
                <el-button type="primary" @click="long('ruleForm')">登录<i v-show="isshow" class="el-icon-loading"></i></el-button>
                <el-button>注册</el-button>
              </el-form-item>
            </el-form>
          </div>
      </div>
      </div>
  </div>
</template>

<script>
// import {mapMutations} from 'vuex'
import {debounce} from '../untils/common'
export default {
  data(){
    var validateusername = (rule, value, callback) => {
        let reg = /^[\u4e00-\u9fa5A-z]{2,18}$/
        if(!reg.test(value)){
          return callback(new Error('账号是由汉字、字母2-18位组成的'))
        }
        callback()
      };
    return{
      isshow:false,
      form:{
        username:'admin',
        pass:'123456'
      },
      rules: {
          username: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            { validator: validateusername, trigger: 'blur' }
          ],
          pass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 2, max: 18, message: '长度在 2 到 18 个字符', trigger: 'blur' }
          ],
      }
    }
  },
  methods:{
  // ...mapMutations(['gettoken']),
  long:debounce(function (formName){
        this.$refs[formName].validate(async (valid) => {
          this.isshow = true
          if (valid) {
            let res = await this.http.post('/administrator/checklogin2',this.form)
            // console.log(res)
            if(res.errCode == '10000'){
              // this.isshow = false
              sessionStorage.setItem('token',res.data.token)
              this.$store.commit('gettoken', res.data.token);
              this.$store.commit('getusername',this.form.username)
              this.$router.push('home')
              this.$store.commit('SETMENU',res.data.menu)
              this.$message.success('登录成功')
            }else{
              this.$message.error(res.errMsg)
            }
            this.isshow = false
          } else {
            // console.log('error submit!!');
            this.$message.error('请输入完账号和密码在进行登录')
            return false;
          }
        }) 
    },1000)
  }
}
</script>
<style lang="less" scoped>
  @import '../assets/css/longin.css';
</style>